.conteudoHorizontalEdicao {
    display: flex;
    align-items: center;
    margin-bottom: 0;
    border: 2px solid #313131;
  }

  .SubtextoEdicao {
    align-items: center;
    justify-content: center;
    width: 70%;
    margin: 0 auto;
  }

  .h2inicial{
    color: whitesmoke;
    font-size: 1.5rem;
    text-align: center;
    margin: 0 auto;
  }

  .videosHorizontal {
    width: 60%;
    display: block;
    margin: 0 auto;
    box-shadow: 0 4px 30px rgba(1, 1, 1, 0.2);
    margin-top: 2%;
}

.videosVertical {
    width: 60%;
    display: block;
    margin: 0 auto;
    box-shadow: 0 4px 30px rgba(1, 1, 1, 0.2);
    margin-top: 2%;
}

.SubtituloPaginaEdicao {
    color: #A790E0;
    font-size: 5rem;
    font-weight: 100;
    text-align: center;
    font-family: 'Poetsen One';
    margin-top: 10%;
  }

  .imagemEdicao {
    width: 70%;
    margin-left: 6em; /* Espaçamento entre a imagem e o parágrafo */
    margin-right: 5em;
    border:  1px solid white;
    border-radius: 50px;
    box-shadow: 0 4px 30px rgba(1, 1, 1, 0.2);
    transition: 0.5s;
  }
  
  

  .overlayPortE:hover .imagemEdicao {
    opacity: 0.2; /* Faz a imagem ficar branca */
    transform: scale(1.05); /* Aumenta o tamanho */
    border: white 2px solid;
    border-radius: 50px;
  }
  
  .overlayPortE {
    position: relative;
    display: inline-block;
    padding: 1em;
  }
  
  .overlayPortE:hover .overlayPortE-text {
    opacity: 1;
  }
  
  .overlayPortE-text {
    position: absolute;
    top: 50%;
    left: 57%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 1.5rem;
    font-family: "Overpass Mono", serif;
    text-align: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none; /* Garante que o texto não interfere no hover */
  }

  .arrowdown{
    width: 5em;
    margin: 0 auto;
  }
  
